<template>
  <div class="about">
    <h1>This is an protected page !!</h1>
    {{token}}
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  export default{
    computed:{
      ...mapState(['token'])
    },
    methods:{
      async dataProtected(){
        try {
          const res = await fetch('http://localhost:8000/admin',{
          headers: {
            'Content-Type': 'application/json',
            'Authorization': this.token
          },
        })
          const data = await res.json()
          console.log(data)
        } catch (err) {
          console.log(err);
        }
      }
    },
    created(){
      this.dataProtected()
    }
  }
</script>
